@keyframes spin {
    from{
        transform: rotate(0);
    }
    to{
        transform: rotate(360deg);
    }
    
}

.loading-module{
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    align-items: center;    
}
.loading-module img{
    animation: spin infinite 3s linear;
    max-width: 400px;
}