html,body{
  max-width: 100vw;
  min-height: 100vh;
  height: 100vh;
  overflow-x: hidden;  
}
body{
  background-image: url("data:image/jpeg;base64,");  
  font-family:'Raleway', sans-serif ; 
  
}
.App {
  display: flex;
  flex-direction: column;   
  max-width: 100vw;;
  min-height: 100vh; 
  height: 100vh;
  text-align: center;  
}

#loginPage{
  width: 100%;
  height: 100%;
  display: flex; 
  align-items: center;
  justify-content: center;  
}

#loginPage form{
  width: 100%;
  max-width: 600px;
}
.flex{
  display: flex;
  align-items: center; 
  flex-wrap: wrap; 
}
.isActive{
  border-bottom: 3px solid white;
}

.pointer{
  cursor: pointer;
}

.pointer:hover{
  transform: translateY(3px);
}

/* Fade In Animation */
@keyframes fade-in {
  from{opacity: 0;}
  to{opacity: 1;}
}

.fade-in{
  animation: fade-in 2s;
}


.row{
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
}

.error{
  color:red;
  text-shadow: 0px 0px 1px white;
}

/* Form */
 form{ 
 max-width: 600px; 
 background-color: aliceblue;
 border-radius: 5px;
 padding:25px;
 display:flex;
 flex-direction: column;  
}

/* Text Format */
.text-start{
  text-align: left;
}